/**
 * @
 */

import React, { Component } from 'react';
import PropTypes from 'prop-types';

/**
 *   mobileOrToken      token    string    [][15012345678]
     smsType                string    []00:;02:;20:;
     userType               number    [](:2;:1000)
     verifyCode             string    [][4][6891]
     verifyNoncestr         string    [][32]""noncestr
 * @param data
 * @returns {service}
 */
const propTypes = {
  title: PropTypes.string,
  timer: PropTypes.number,
  // onCLick promise
  onClick: PropTypes.func.isRequired,
  resetTool: PropTypes.func
};
const defaultProps = {
  timer: 60,
  title: '',
  countdownTitle: '',
  pendingTitle: '...',
  errorTitle: ',',
  styleName: ''
};

export default class SendSmsCodeBtn extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isCounting: false,
      remainTime: -1,
      errMsg: ''
    };
    this._initState = { ...this.state };
  }

  clean() {
    this.isCounting = false;
    this._timer && clearInterval(this._timer);
  }
  resetState() {
    this.setState(this._initState);
  }

  startTimer() {
    if (this.isCounting) return;
    this.isCounting = true;
    this.setState({
      isCounting: true,
      remainTime: this.props.timer
    });
    this._timerStartPoint = Date.now();
    this._timer = setInterval(() => {
      if (this.state.remainTime === 0) {
        this.clean();
        this.resetState();
      } else {
        this.setState({
          remainTime:
            this.props.timer -
            (((Date.now() - this._timerStartPoint) / 1000) | 0)
        });
      }
    }, 1000);
  }
  handleClick() {
    if (this.state.isPending || this.isCounting) return;

    this.setState({
      isPending: true,
      errMsg: ''
    });

    this.props
      .onClick()
      .then(() => {
        this.setState({ isPending: false });
        this.startTimer();
      })
      .catch(msg => {
        this.setState({
          isPending: false,
          errMsg: typeof msg === 'string' ? msg : this.props.errorTitle
        });
        this.clean();
        this.resetState();
      });
  }
  componentWillMount() {
    this.clean();
  }
  componentDidMount() {
    this.props.resetTool &&
      this.props.resetTool(() => {
        this.clean();
        this.resetState();
      });
  }
  componentWillUnmount() {
    this.clean();
  }

  render() {
    const { errMsg, isPending, isCounting } = this.state;
    return (
      <a
        className={`u-button c-btn-countdown ${this.props.styleName} ${
          isCounting || isPending ? 'disable' : ''
        }`}
        onClick={this.handleClick.bind(this)}
      >
        {isPending
          ? this.props.pendingTitle
          : errMsg
            ? errMsg
            : !isCounting
              ? this.props.title
              : this.state.remainTime + this.props.countdownTitle}
      </a>
    );
  }
}

SendSmsCodeBtn.propTypes = propTypes;
SendSmsCodeBtn.defaultProps = defaultProps;



