import React, { Component } from 'react';

import {
  FormWrap,
  FormWrapSty1,
  FormWrapSty2,
  FormText,
  FormTextarea,
  FormRadio,
  FormCheckBox,
  FormPassword,
  ButtonSubmit,
  FormStatic,
  FormSmsCode,
  FormImgCode
} from '../src/components/react/index.jsx';

import '../src/components/scss/index.scss';

import FormController from '../src/FormController';

export class FormDemo extends Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    this.initFormCtrl();
  }
  initFormCtrl() {
    const fc = (this.fc = new FormController({
      debug: true,
      $container: this.wrapper,
      blurChk: true,

      defFormData: {
        mobile: '',
        smsCode: '',
        passwd: '',
        repeatPwd: '',
        radio1: '',
        checkbox1: '',
        checkbox2: '3,4',
        remark: ''
      },
      dataFilter: {
        mobile: val => val.replace(/[^\d]/g, '').slice(0, 11)
      },
      chkVal: {
        mobile: {
          require: val => {
            //debugger
            return val.length === 11;
          }
        },
        smsCode: {
          require: val => +val.length === 6,
          verify: val =>
            new Promise((resolve, reject) => {
              console.log('async check "smsCode" valid.');
              setTimeout(() => resolve(true), 100);
            })
        },
        passwd: {
          require: val => !!val,
          strong: val => /[a-zA-Z]/.test(val) && /[0-9]/.test(val)
        },
        repeatPwd: {
          require: val => !!val,
          sameCheck: val => val === fc.formdata.passwd
        },
        radio1: {
          require: val => !!val
        },
        checkbox1: {
          require: val => !!val
        },
        checkbox2: {
          // no rule
          //require: val =>true
        }
      },
      errMsg: {
        mobile: {
          require: '11'
        },

        imgCode: {
          require: '4',
          valid: ''
        },
        smsCode: {
          require: '6',
          verify: ''
        },
        passwd: {
          require: '',
          strong: ''
        },
        repeatPwd: {
          require: '',
          sameCheck: ''
        },
        radio1: {
          require: ''
        },
        checkbox1: {
          require: ''
        }
      }
    }).on('submit', formdata => {
      console.log(formdata);
      alert('data:'+ JSON.stringify(formdata))
    }));
  }

  handleGetSmsCode() {
    return new Promise((resolve, reject) => {
      // 
      // 
      const promiseChk = this.fc.getChkStatus('mobile' /* name */);
      promiseChk.then(([mobile /*boolean*/ /* name */]) => {
        if (mobile) {
          console.log('', this.fc.formdata.mobile);
          // request backend api to send sms.
          setTimeout(() => resolve(), 200);
        } else {
          reject(''); // 
        }
      });
    });
  }
  render() {
    return (
      <div className="demo-form">
        <h2>Form control demo</h2>
        <FormWrapSty2 _ref={dom => (this.wrapper = dom)}>
          <FormStatic label="">
            <span>13800000000()</span>
          </FormStatic>
          <FormText
            type="tel"
            name="mobile"
            label=""
            placeholder=""
          />
          <FormSmsCode
            maxLen={6}
            name="smsCode"
            label=""
            placeholder=""
            onGetSmsCodeClick={this.handleGetSmsCode.bind(this)}
            btnName=""
            btnNameCountdown="s"
            timer={10}
          />
          <FormPassword
            name="passwd"
            label=""
            placeholder="6-16"
          />
          <FormPassword
            name="repeatPwd"
            label=""
            placeholder=""
          />
          <FormRadio
            name="radio1"
            label=""
            options={[
              { desc: '', value: '1' },
              { desc: '', value: '0' }
            ]}
          />
          <FormCheckBox
            name="checkbox1"
            label=""
            options={[
              { desc: '1', value: '0' },
              { desc: '2', value: '1' }
            ]}
          />

          <FormCheckBox
            name="checkbox2"
            label=""
            options={[
              { desc: '1', value: '2' },
              { desc: '2', value: '3' },
              { desc: '3', value: '4' }
            ]}
          />

          <FormTextarea
            name="remark"
            label=""
            placeholder=""
          />
          <div className="button-wrap">
            <ButtonSubmit className="button-big" isLoading={false}>
              
            </ButtonSubmit>
          </div>
        </FormWrapSty2>
      </div>
    );
  }
}



